.subscription__period-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  gap: 46px;
  border-bottom: 2px solid rgba(120, 120, 120, 0.2);
  order: -1;
}

.subscription__button {
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 30px;
  display: block;
  padding: 20px 0;
  color: rgba(16, 38, 84, 0.5019607843);
  cursor: pointer;
}
.subscription__button:hover {
  color: #102654;
}

.subscription__price-list {
  position: relative;
  z-index: 1;
  list-style: none;
  margin: 0;
  padding: 0;
  display: none;
  grid-template-columns: repeat(3, minmax(200px, 360px));
  gap: 40px;
}
@media (max-width: 1199px) {
  .subscription__price-list {
    grid-template-columns: 442px;
  }
}
@media (max-width: 767px) {
  .subscription__price-list {
    grid-template-columns: 1fr;
  }
}

.subscription__image {
  position: absolute;
  right: 0;
  bottom: -124px;
}
@media (max-width: 767px) {
  .subscription__image {
    display: none;
  }
}

.subscription__button--active {
  border-bottom: 2px solid #d10a42;
  color: #102654;
}

.subscription__price-list--active {
  display: grid;
}

.price__item {
  padding: 61px 0;
  background: #ffffff;
  aspect-ratio: 360/448;
  overflow: hidden;
  border: 7px solid transparent;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.price__item:hover {
  border: 7px solid skyblue;
  border-image: linear-gradient(to left top, #00359f, #d10a42);
  border-image-slice: 1;
}
.price__item:hover .price__button {
  background: linear-gradient(97.05deg, #ed0233 17.6%, #003eb7 95.31%);
  color: #ffffff;
  border: transparent;
}
.price__title {
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 26px;
  line-height: 29px;
  margin: 0;
  color: #102654;
  text-align: center;
}

.price__info {
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  display: block;
  text-align: center;
  color: #495a7d;
  margin-bottom: 96px;
}

.price__number-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 14px;
}

.price__number-shadow {
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 100px;
  line-height: 150px;
  color: #e8e8e8;
  opacity: 0.48;
  position: absolute;
  top: -53px;
  left: 50%;
  transform: translateX(-50%);
}

.price__number {
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 60px;
  line-height: 90px;
  position: relative;
  z-index: 1;
  margin: 0;
  color: #103aae;
  text-align: center;
}

.price__number-icon {
  position: relative;
  z-index: 1;
}

.price__button {
  width: 206px;
  margin-top: auto;
}

.price__button--border {
  border: 2px solid #102654;
  color: #102654;
}

.super-games__container {
  display: grid;
  grid-template-columns: minmax(300px, 500px) auto;
  justify-content: center;
  padding: 120px 103px;
}
@media (max-width: 1199px) {
  .super-games__container {
    padding: 100px 45px;
    grid-template-columns: minmax(200px, 292px) auto;
  }
}
@media (max-width: 767px) {
  .super-games__container {
    padding: 70px 0;
    grid-template-columns: 1fr;
  }
}

.super-games__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 79px;
  background: #ED0233;
  color: #FFFFFF;
}
@media (max-width: 1199px) {
  .super-games__wrapper {
    padding: 0 45px;
  }
}

.super-games__info {
  display: grid;
  grid-template-columns: min-content auto;
  justify-content: space-between;
  align-items: start;
  gap: 20px;
}

.super-games__title {
  text-align: center;
  letter-spacing: 0.03em;
}
@media (max-width: 1199px) {
  .super-games__title {
    font-size: 48px;
    line-height: 46px;
  }
}

.super-games__description-wrapper {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
@media (max-width: 1199px) {
  .super-games__description-wrapper {
    gap: 5px;
  }
}

.super-games__description-list {
  display: grid;
  grid-template-columns: min-content;
  margin: 0;
}

.super-games__description-term {
  margin: 0;
  padding-bottom: 13px;
  border-bottom: 1px solid #FFFFFF;
}
@media (max-width: 1199px) {
  .super-games__description-term {
    padding-bottom: 7px;
  }
}

.super-games__description-definition {
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 60px;
  margin: 0;
  display: flex;
  align-items: center;
  gap: 7px;
}
@media (max-width: 1199px) {
  .super-games__description-definition {
    font-family: "Roboto-Condensed", "Arial", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 36px;
  }
}

.super-games__description-text {
  font-family: "Roboto-Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1199px) {
  .super-games__description-text {
    font-family: "Roboto-Condensed", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 18px;
  }
}

.super-games__text {
  margin: 0;
  margin-top: 30px;
}
@media (max-width: 1199px) {
  .super-games__text {
    margin-top: 15px;
  }
}

.super-games__button {
  margin-top: 55px;
}
@media (max-width: 1199px) {
  .super-games__button {
    padding: 16px;
    margin-top: 36px;
  }
}

.super-games__image {
  object-fit: cover;
}
@media (max-width: 767px) {
  .super-games__image {
    width: 100%;
  }
}

